title-height / 2) - 1px;\n            border-right: none;\n            border-left: 1px solid @border-color-base;\n          }\n        }\n      }\n    }\n  }\n  // >>> Checkbox\n  &-checkbox {\n    .@{tree-prefix-cls}-rtl & {\n      margin: ((@tree-title-height - @checkbox-size) / 2) 0 0 8px;\n    }\n  }\n}\n\n.@{select-tree-prefix-cls} {\n  // >>> Checkbox\n  &-checkbox {\n    .@{tree-prefix-cls}-select-dropdown-rtl & {\n      margin: ((@tree-title-height - @checkbox-size) / 2) 0 0 8px;\n    }\n  }\n}\n","@import '../../style/themes/index';\n@import '../../style/mixins/index';\n@import '../../tree/style/mixin';\n@import '../../checkbox/style/mixin';\n\n@tree-select-prefix-cls: ~'@{ant-prefix}-tree-select';\n@select-tree-prefix-cls: ~'@{ant-prefix}-select-tree';\n\n.antCheckboxFn(@checkbox-prefix-cls: ~'@{select-tree-prefix-cls}-checkbox');\n\n.@{tree-select-prefix-cls} {\n  // ======================= Dropdown =======================\n  &-dropdown {\n    padding: @padding-xs (@padding-xs / 2);\n\n    &-rtl {\n      direction: rtl;\n    }\n    // ======================== Tree ========================\n    .@{select-tree-prefix-cls} {\n      border-radius: 0;\n\n      &-list-holder-inner {\n        align-items: stretch;\n\n        .@{select-tree-prefix-cls}-treenode {\n          .@{select-tree-prefix-cls}-node-content-wrapper {\n            flex: auto;\n          }\n        }\n      }\n    }\n  }\n}\n\n.@{select-tree-prefix-cls} {\n  .antTreeFn(@select-tree-prefix-cls);\n\n  // change switcher icon rotation in rtl direction\n  & &-switcher {\n    &_close {\n      .@{select-tree-prefix-cls}-switcher-icon {\n        svg {\n          .@{tree-select-prefix-cls}-dropdown-rtl & {\n            transform: rotate(90deg);\n          }\n        }\n      }\n    }\n\n    &-loading-icon {\n      .@{tree-select-prefix-cls}-dropdown-rtl & {\n        transform: scaleY(-1);\n      }\n    }\n  }\n}\n","@import '../../style/themes/index';\n@import '../../style/mixins/index';\n\n@typography-prefix-cls: ~'@{ant-prefix}-typography';\n\n// =============== Basic ===============\n.@{typography-prefix-cls} {\n  color: @text-color;\n  overflow-wrap: break-word;\n\n  &&-secondary {\n    color: @text-color-secondary;\n  }\n\n  &&-success {\n    color: @success-color;\n  }\n\n  &&-warning {\n    color: @warning-color;\n  }\n\n  &&-danger {\n    color: @error-color;\n\n    a&:active,\n    a&:focus {\n      color: @error-color-active;\n    }\n\n    a&:hover {\n      color: @error-color-hover;\n    }\n  }\n\n  &&-disabled {\n    color: @disabled-color;\n    cursor: not-allowed;\n    user-select: none;\n  }\n\n  // Tag\n  div&,\n  p {\n    .typography-paragraph();\n  }\n\n  h1&,\n  div&-h1,\n  div&-h1 > textarea,\n  h1 {\n    .typography-title-1();\n  }\n\n  h2&,\n  div&-h2,\n  div&-h2 > textarea,\n  h2 {\n    .typography-title-2();\n  }\n\n  h3&,\n  div&-h3,\n  div&-h3 > textarea,\n  h3 {\n    .typography-title-3();\n  }\n\n  h4&,\n  div&-h4,\n  div&-h4 > textarea,\n  h4 {\n    .typography-title-4();\n  }\n\n  h5&,\n  div&-h5,\n  div&-h5 > textarea,\n  h5 {\n    .typography-title-5();\n  }\n\n  h1&,\n  h2&,\n  h3&,\n  h4&,\n  h5& {\n    .@{typography-prefix-cls} + & {\n      margin-top: @typography-title-margin-top;\n    }\n  }\n\n  div,\n  ul,\n  li,\n  p,\n  h1,\n  h2,\n  h3,\n  h4,\n  h5 {\n    + h1,\n    + h2,\n    + h3,\n    + h4,\n    + h5 {\n      margin-top: @typography-title-margin-top;\n    }\n  }\n\n  a&-ellipsis,\n  span&-ellipsis {\n    display: inline-block;\n    max-width: 100%;\n  }\n\n  a&,\n  a {\n    .operation-unit();\n    text-decoration: @link-decoration;\n\n    &:active,\n    &:hover {\n      text-decoration: @link-hover-decoration;\n    }\n\n    &[disabled],\n    &.@{typography-prefix-cls}-disabled {\n      color: @disabled-color;\n      cursor: not-allowed;\n\n      &:active,\n      &:hover {\n        color: @disabled-color;\n      }\n\n      &:active {\n        pointer-events: none;\n      }\n    }\n  }\n\n  code {\n    margin: 0 0.2em