lock\n  pre,\n  blockquote {\n    margin: 1em 0;\n  }\n\n  pre {\n    padding: 0.4em 0.6em;\n    white-space: pre-wrap;\n    word-wrap: break-word;\n    background: rgba(150, 150, 150, 0.1);\n    border: 1px solid rgba(100, 100, 100, 0.2);\n    border-radius: 3px;\n\n    // Compatible for marked\n    code {\n      display: inline;\n      margin: 0;\n      padding: 0;\n      font-size: inherit;\n      font-family: inherit;\n      background: transparent;\n      border: 0;\n    }\n  }\n\n  blockquote {\n    padding: 0 0 0 0.6em;\n    border-left: 4px solid rgba(100, 100, 100, 0.2);\n    opacity: 0.85;\n  }\n\n  // ============ Ellipsis ============\n  &-single-line {\n    white-space: nowrap;\n  }\n\n  &-ellipsis-single-line {\n    overflow: hidden;\n    text-overflow: ellipsis;\n\n    // https://blog.csdn.net/iefreer/article/details/50421025\n    a&,\n    span& {\n      vertical-align: bottom;\n    }\n  }\n\n  &-ellipsis-multiple-line {\n    /* stylelint-disable-next-line value-no-vendor-prefix */\n    display: -webkit-box;\n    overflow: hidden;\n    -webkit-line-clamp: 3;\n\n    /*! autoprefixer: ignore next */\n    -webkit-box-orient: vertical;\n  }\n}\n\n@import './rtl';\n","// =============== Common ===============\n.typography-paragraph() {\n  margin-bottom: 1em;\n}\n\n.typography-title(@fontSize; @fontWeight; @lineHeight; @headingColor; @headingMarginBottom;) {\n  margin-bottom: @headingMarginBottom;\n  color: @headingColor;\n  font-weight: @fontWeight;\n  font-size: @fontSize;\n  line-height: @lineHeight;\n}\n\n.typography-title-1() {\n  .typography-title(\n    @heading-1-size,\n    @typography-title-font-weight,\n    1.23,\n    @heading-color,\n    @typography-title-margin-bottom\n  );\n}\n.typography-title-2() {\n  .typography-title(\n    @heading-2-size,\n    @typography-title-font-weight,\n    1.35,\n    @heading-color,\n    @typography-title-margin-bottom\n  );\n}\n.typography-title-3() {\n  .typography-title(\n    @heading-3-size,\n    @typography-title-font-weight,\n    1.35,\n    @heading-color,\n    @typography-title-margin-bottom\n  );\n}\n.typography-title-4() {\n  .typography-title(\n    @heading-4-size,\n    @typography-title-font-weight,\n    1.4,\n    @heading-color,\n    @typography-title-margin-bottom\n  );\n}\n.typography-title-5() {\n  .typography-title(\n    @heading-5-size,\n    @typography-title-font-weight,\n    1.5,\n    @heading-color,\n    @typography-title-margin-bottom\n  );\n}\n","@import '../../style/themes/index';\n@import '../../style/mixins/index';\n\n@typography-prefix-cls: ~'@{ant-prefix}-typography';\n\n.@{typography-prefix-cls} {\n  &-rtl {\n    direction: rtl;\n  }\n\n  // Operation\n  &-expand,\n  &-edit,\n  &-copy {\n    .@{typography-prefix-cls}-rtl & {\n      margin-right: 4px;\n      margin-left: 0;\n    }\n  }\n\n  &-expand {\n    .@{typography-prefix-cls}-rtl & {\n      float: left;\n    }\n  }\n\n  // Text input area\n  &-edit-content {\n    div& {\n      &.@{typography-prefix-cls}-rtl {\n        right: -@input-padding-horizontal - 1px;\n        left: auto;\n      }\n    }\n\n    &-confirm {\n      .@{typography-prefix-cls}-rtl & {\n        right: auto;\n        left: 10px;\n      }\n    }\n  }\n\n  // list\n  ul,\n  ol {\n    li {\n      .@{typography-prefix-cls}-rtl& {\n        margin: 0 20px 0 0;\n        padding: 0 4px 0 0;\n      }\n    }\n  }\n}\n","@import '../../style/themes/index';\n@import '../../style/mixins/index';\n\n@upload-prefix-cls: ~'@{ant-prefix}-upload';\n@upload-item: ~'@{ant-prefix}-upload-list-item';\n@upload-picture-card-size: 104px;\n@upload-picture-card-border-style: @border-style-base;\n\n.@{upload-prefix-cls} {\n  .reset-component();\n\n  outline: 0;\n\n  p {\n    margin: 0;\n  }\n\n  &-btn {\n    display: block;\n    width: 100%;\n    outline: none;\n  }\n\n  input[type='file'] {\n    cursor: pointer;\n  }\n\n  &&-select {\n    display: inline-block;\n  }\n\n  &&-disabled {\n    cursor: not-allowed;\n  }\n\n  &&-select-picture-card {\n    width: 